.Result {
    display: flex;
    flex-direction: column;
    color: white;
    animation: appear 1s linear 2s;
    opacity: 0;
    animation-fill-mode: forwards;
}

.ResultTitle {
    font-size: 40px;
    margin-bottom: 10px;
}

@keyframes appear {
    100% {
        opacity: 1;
    }
}

@media(max-width: 650px) {
    .Result {
        order: 3;
    }
}