.Pick {
    position: relative;
    height: 164px;
    display: flex;
    justify-content: space-evenly;
    color: white;
    font-size: 10px;
    flex-direction: column;
}

@media(max-width: 650px) {
    .Pick {
        flex-direction: column-reverse;
        height: 130px;
        justify-content: space-between;
        width: 50%;
    }
}